body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #FAFAFA;
}

.App {
  text-align: center;
  overflow-y: scroll;
}

.App-logo {
  position: relative;
  top: 5vmin;
  /*animation: App-logo-spin infinite 20s linear;*/
  height: 150px;
  pointer-events: none;
}

.App-title {
  position: fixed;
  -webkit-align-self: left !important;
          align-self: left !important;
  height: 50px;
  margin-top: 25px;
  margin-left: -15%;
}

.App-name {
  position: relative;
  top: 5vmin;
  pointer-events: none;
}

.App-header {
  position: fixed;
  background-color: #F44336;
  left: 50%;
  width: 50%;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  /*justify-content: center;*/
  font-size: calc(10px + 2vmin);
  color: white;
}

#Background {
  position: fixed;
  background-color: rgb(190, 9, 190);
  top: 0px;
  left: 0px;
  width: 50%;
  height: 100%;
  z-index: 1;
}

.App-link {
  color: #61dafb;
}
.Home {
  height: 1000px;
  width:  100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  justify-items: center;
}

.home-row-medium {
  width:  95%;
  left:  3%;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}
.home-colm {
  -webkit-flex: 1 1;
          flex: 1 1;
}
.home-row {
  justify-items: center;
}
.home-status {
  height: 240px !important;
  width: 40%;
  -webkit-flex-direction: column !important;
          flex-direction: column !important;
  display: -webkit-flex;
  display: flex;
}

.Button {
  margin-left: 5%;
  margin-top: 10px;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.50);
  border-radius: 20px;
  height: 120px;
  width: 90%;
  background: #FFFFFF;
  color: #000000;
  font-family: Roboto-Bold;
  font-size: 18px;
  text-align: left;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
}

.Button:hover {
  background: #E9EDF1;
}
.Button:active {
  background: rgb(208, 211, 214);
}

.btn-image {
  margin-left:10px;
  size: 10px;
  width: 80%;
  height: auto;
}

.Status {
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.50);
  border-radius: 20px;
}

.Menu {
  top: 0px;
  width: 100%;
  height: 100px;
  background: #BB0F03;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  
}

.Menu-button {
  font-family: Roboto-Bold;
  font-size: 34px;
  width: 30%;
  color: #FFFFFF;
  background: none;
  padding: 0;
  border: none;
  outline: none; 
  text-align: center;
}
.Menu-button:hover {
  cursor: pointer;
  color: #E9EDF1;
  }
  
.Menu-button:active {
  outline: none;
  border: none;
  color: #E9EDF1;
}
  
.Menu-button:focus {
  outline:0;
  border-radius: 30px;
  margin-top: 8%;
  height: 80%;
  border-radius: 20px;
  background-color: #F44336;
}

.Left-side {
  position: fixed;
  top: 0px;
  width: 50%;
}

.left-body {
  direction: row;
  justify-items: center;
}

.Left {
  background: #E9EDF1;
}

@media only screen and (max-height: 800px) {
  .Button {
    height: 80px;
    font-size: 10px;
  }
  .btn-image {
    width: 45%;
    height: auto;
  }
  .home-status {
    height: 160px !important;
  }
}
@media only screen and (max-width: 900px) {
  /* For mobile: */
  .Left-side {display: none;}
  .App-logo {display: none;}
  .App-title {display: none;}
  .App-title {display: none;}
  .App-header{  
    left: 0%;
    width: 100%;}
  .home-status {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-left:0% !important;
    height: 130px !important;
  }
  .Button {
    font-size: 10px;
    height: 60px;
  }
  .btn-image {
    size: 5px;
    width: 40%;
    height: auto;
  }
  .Menu {
    height: 50px;
  }
  .Menu-button {
    font-size: 15px;
  }
}

@media only screen and (min-width: 1001px) {
  /* For desktop: */
  .hover-left {
    margin-left:-20% !important;
    width:115% !important;
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body::after, body::before {
  /* these are the 2 half blocks which cover the content once the animation is triggered */
  height: 50vh;
  width: 100%;
  position: fixed;
  left: 0;
}
body::before {
  top: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
body::after {
  bottom: 0;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
}
body.page-is-changing::after, body.page-is-changing::before {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

